/**
 * Services block editor styles
 */

 @use "sass:color";
 @use "../../scss/index" as *;
 @import './style.scss';
 
 .obx-services {
   max-width: 100%;
     &-main {
         &__background {
             &::before {
                 content: '';
                 position: absolute;
                 top: 0;
                 left: 0;
                 width: 100%;
                 height: 100%;
                 background-color: rgba(0, 0, 0, 0.3);
                 z-index: 1;
             }
         }
 
         &__content {
             background-color: rgba(255, 255, 255, 0.15);
         }
     }
 
     .obx-service-item {
         position: relative;
         margin-bottom: 2rem;
         padding: 2rem;
         border: 1px solid #ddd;
         border-radius: 4px;
         flex-wrap: wrap;
 
         &__actions {
             display: flex;
             justify-content: center;
             margin-top: 1.5rem;
             padding-top: 1rem;
             border-top: 1px solid #ddd;
             width: 100%;
         }
 
         &__remove {
             width: auto;
             justify-content: center;
         }
 
         &__image {
             position: relative;
             background-color: #f5f5f5;
             border: 2px dashed #ddd;
 
             &::before {
                 content: 'Click to upload image';
                 position: absolute;
                 top: 50%;
                 left: 50%;
                 transform: translate(-50%, -50%);
                 color: #999;
                 font-size: 14px;
             }
 
             &.has-image::before {
                 display: none;
             }
         }
     }
 
     .components-button {
         &.is-small {
             padding: 0 8px;
             height: 30px;
             font-size: 12px;
         }
     }
 
     .components-base-control {
         margin-bottom: 16px;
     }
 
     .components-panel__row {
         margin-bottom: 16px;
     }
 }
 
 /**
  * Services block styles for the editor
  */
 
 .wp-block-obx-blocks-services {
   // Editor-specific styles
   
   .obx-services__cta-wrapper {
     position: relative;
     
     .obx-services__cta-text {
       padding: 8px;
       border: 1px dashed #ddd;
       border-radius: 4px;
       
       &:focus {
         border-color: #007cba;
       }
     }
     
     .obx-services__cta-url-button {
       position: absolute;
       top: 8px;
       right: 8px;
       padding: 2px;
       background: rgba(255, 255, 255, 0.8);
       border-radius: 2px;
       
       &:hover {
         background: #fff;
       }
     }
     
     .obx-services__cta-url-input {
       margin-top: 8px;
       padding: 8px;
       background: #f0f0f0;
       border-radius: 4px;
       
       .block-editor-url-input {
         width: 100%;
       }
     }
     
     .obx-services__cta-preview {
       margin-top: $spacing-md;
       padding: $spacing-sm;
       background-color: rgba(0, 0, 0, 0.05);
       border-radius: 4px;
       
       span {
         margin-right: $spacing-sm;
         font-style: italic;
       }
       
       .obx-services__cta-link {
         display: inline-block;
         padding: $spacing-xs $spacing-md;
         font-family: $font-ui;
         font-weight: 500;
         font-size: 0.9rem;
         text-align: center;
         text-decoration: none;
         cursor: pointer;
         border-radius: 4px;
         border: 1px solid transparent;
         transition: all 0.3s ease;
         background-color: $color-secondary;
         color: white;
         
         &:hover, &:focus {
           background: rgba($color-primary, 0.85);
           backdrop-filter: blur(4px);
           -webkit-backdrop-filter: blur(4px);
           box-shadow: 0 8px 32px 0 rgba($color-primary, 0.2);
           transform: translateY(-2px);
           border: 1px solid rgba(255, 255, 255, 0.18);
         }
       }
     }
   }
   
   .obx-services__item {
     cursor: pointer;
     position: relative;
     
     &.is-selected {
       outline: 2px solid #007cba;
     }
     
     &-actions {
       margin-top: 1rem;
       opacity: 0;
       transition: opacity 0.2s ease;
     }
     
     &:hover &-actions {
       opacity: 1;
     }
     
     &-remove {
       font-size: 0.8rem !important;
       height: auto !important;
       padding: 4px 8px !important;
     }
     .obx-services__title-wrapper {
       display: flex;
       align-items: center;
       gap: 1rem;
       .obx-services__item-icon {
         &-wrapper {
           img {
             max-width: 40px;
             max-height: 40px;
           }
         }
       }
 
     }
   }
   
   .obx-services__add-button {
     display: inline-flex;
     margin-top: 2rem;
     
     svg {
       margin-right: 8px;
     }
   }
   
 }
 
 .obx-service-item-editor {
     padding: 20px;
     margin-bottom: 20px;
     background: #f8f9fa;
     border-radius: 8px;
     border: 1px solid #e2e4e7;
 
     h4 {
         margin: 0 0 15px;
         padding-bottom: 10px;
         border-bottom: 1px solid #e2e4e7;
         font-size: 14px;
         font-weight: 600;
         color: #1e1e1e;
     }
 
     .components-base-control {
         margin-bottom: 16px;
     }
 
     .components-button {
         margin: 5px 0;
     }
 
     .editor-post-featured-image__toggle {
         width: 100%;
         justify-content: center;
         margin: 10px 0;
     }
 
     .components-toggle-control {
         margin: 15px 0;
     }
 }
 
 .obx-services-main {
     &__content {
         background: rgba(255, 255, 255, 0.9);
         backdrop-filter: blur(10px);
     }
 }
 
 .obx-service-item {
     &__content {
         background: rgba(196, 164, 104, 0.2);
     }
 } 